
.column__name {
  display: block;
  background-color: #00d1b2;
  border-radius: 6px;
  color: #fff;
  position: relative;
}

.column__name.is-danger{
  background-color: #f14668;
}

.column__name .delete {
  right: .1rem;
    position: absolute;
    top: .18rem;
  display: none;
}

.column__name:hover .delete{
   display: inline-block;
}